body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.cart-item{
display:inline-flex;
margin: 20px 10px;
padding: 20px;
border:2px solid black;
border-radius: 1rem;
background-color: honeydew;
margin-left: 32px;
}
.left-block{
  display:inline-flex;
  margin: 3px 30px;
}
.cart-item .right-block{
padding: 0 20px;
}
.cart-item-actions{
  margin-top: 20px;
}
.action-icons{
  height: 25px;
  width:25px;
  margin-right:10px;
}
.action-icons:hover{
  cursor: pointer;
}